import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import appData from '../appData';
import { AuthenticationService } from '../services/authenticate.service';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.scss']
})
export class UserProfileComponent implements OnInit {
  @ViewChild('userStat') userStat: ElementRef;
  data = appData;
  working = false;
  user = undefined;
  userStatistics = [];

  todaysMoney = {
    shop: 0,
    consoles: 0
  };

  constructor(private authService: AuthenticationService) {}

  ngOnInit() {
    this.authService
      .getUserById(this.data.data.selectedUser.id)
      .subscribe((data: any) => {
        this.user = data;
      });

    this.working = this.data.data.selectedUser.userActive;

    this.authService
      .getUserStats(this.data.data.selectedUser.id)
      .subscribe((data: any) => {
        console.warn(data);
        console.warn(data.getCommonStatic[0].date.split(' ')[0]);
        data.getCommonStatic.forEach((singleDay: any) => {
          const tmpObj = {
            date: singleDay.date.split(' ')[0],
            consoleSum:
              singleDay.aboutUserConsole !== null
                ? this.getSumDay(singleDay.aboutUserConsole[0])
                : 0,
            shopSum:
              singleDay.aboutUserTrade !== null
                ? this.getSumDay(singleDay.aboutUserTrade[0])
                : 0
          };
          this.userStatistics.push(tmpObj);
        });
        // this.getDayleStatistics(data);
      });
  }

  getDayleStatistics(data: any) {
    data.userConsole.forEach(consoleDate => {
      const tmpStats1 = {
        date: consoleDate.date.split(' ')[0],
        shopSum: 0,
        consoleSum: this.getSumDay(consoleDate.aboutUser)
      };

      data.userTrade.forEach(shopDate => {
        if (shopDate.date.split(' ')[0] !== consoleDate.date.split(' ')[0]) {
          const tmpStats2 = {
            date: shopDate.date.split(' ')[0],
            shopSum: this.getSumDay(shopDate.aboutUser),
            consoleSum: 0
          };
          let counter = 0;
          this.userStatistics.forEach(el => {
            if (el.date === shopDate.date.split(' ')[0]) {
              counter++;
            }
          });
          if (counter === 0) {
            this.userStatistics.push(tmpStats2);
          }
        } else {
          tmpStats1.shopSum = this.getSumDay(shopDate.aboutUser);

          let counter = 0;

          for (let i = 0; i < this.userStatistics.length; i++) {
            if (this.userStatistics[i].date === tmpStats1.date) {
              counter = i;
            }
          }
          if (counter === 0) {
            this.userStatistics.push(tmpStats1);
          } else {
            this.userStatistics[counter] = tmpStats1;
          }
        }
      });
    });
  }

  getSumDay(from: any) {
    return from.sumDay;
  }

  changeSize() {
    this.userStat.nativeElement.classList.toggle('extended');
  }
  get userAvatar() {
    return this.data.data.avatars[this.data.data.selectedUser.avatarId - 1];
  }

  startWorking() {
    this.authService.startWorking().subscribe((data: any) => {
      if (data.message) {
        this.data.data.currentModal = 'informationModal';
        this.data.data.modalParams[this.data.data.currentModal].infoText =
          data.message;
        this.data.data.modalParams[this.data.data.currentModal].name =
          'Warning';
        this.data.data.modalParams[this.data.data.currentModal].color = 'red';
      } else {
        this.working = true;
      }
    });
  }

  stopWorking() {
    this.authService.stopWorking().subscribe((data: any) => {
      this.working = false;
      const lastResults = [
        {
          name: ': ',
          quantity: 0,
          price: data.consoleSum
        },
        { name: ': ', quantity: 0, price: data.magazineSum }
      ];
      this.data.data.currentModal = 'userInfoModal';
      this.data.data.modalParams[this.data.data.currentModal].info = [];
      this.data.data.modalParams[this.data.data.currentModal].info.push(
        lastResults
      );
    });
  }

  get currentUser() {
    return this.user !== undefined && this.user;
  }

  getDay(date: string) {
    const days = [
      '',
      '',
      '',
      '',
      '',
      '',
      ''
    ];

    return days[new Date(date).getDay()];
  }

  update() {
    this.data.changeState('addEmployee');
  }
}
