import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

// const url = 'http://playtime.ge';
const url = 'http://localhost:4000';
// const url = 'http://33cbbe80.ngrok.io';

@Injectable()
export class ProductService {
  public user;

  constructor(private http: HttpClient) {}

  addProduct(
    productName: string,
    productPrice: number,
    productQuantity: number,
    productType: string
  ) {
    return this.http.post<any>(
      `${url}/magazines/create`,
      {
        Name: productName,
        Price: productPrice,
        Quantity: productQuantity,
        Type: productType
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  getAllProducts() {
    return this.http.get(`${url}/magazines`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  sellProduct(userID: any, productID: any, quantity: any) {
    console.warn(userID, productID, quantity);
    return this.http.post<any>(
      `${url}/magazines/trade`,
      {
        UserId: userID,
        MagazineId: productID,
        QuantityProduct: quantity
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  getProductById(id: number) {
    return this.http.get(`${url}/magazines/${id}`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  getPriceSum() {
    return this.http.get(`${url}/magazines/tradetoday`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  updateSingleProduct(
    id: number,
    productName: string,
    productPrice: number,
    productQuantity: number,
    productType: string
  ) {
    console.error(id, productName, productPrice, productQuantity, productType);
    return this.http.put(
      `${url}/magazines/${id}`,
      {
        Name: productName,
        Price: productPrice,
        Quantity: productQuantity,
        Type: productType
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  deleteSingleProduct(id: number) {
    return this.http.delete(`${url}/magazines/${id}`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }
}
