import { Component, OnInit, ViewChild, ElementRef, Input } from '@angular/core';
import { ProductService } from '../services/products.service';

@Component({
  selector: 'app-register-product',
  templateUrl: './register-product.component.html',
  styleUrls: ['./register-product.component.scss']
})
export class RegisterProductComponent implements OnInit {
  @ViewChild('productName') productName: ElementRef;
  @ViewChild('productPrice') productPrice: ElementRef;
  @ViewChild('productQuantity') productQuantity: ElementRef;

  @ViewChild('drinkCheck') drinkCheck: ElementRef;
  @ViewChild('chillCheck') chillCheck: ElementRef;
  @ViewChild('hotDrinksCheck') hotDrinksCheck: ElementRef;
  @ViewChild('sandvichCheck') sandvichCheck: ElementRef;

  public secondaryTitle = '';

  @Input() data;

  constructor(private productService: ProductService) {}

  ngOnInit() {
    this.init(this.data.data.selectedProduct !== undefined);
  }

  init(forEdit: boolean) {
    if (forEdit) {
      this.secondaryTitle = '';
      this.productName.nativeElement.value = this.data.data.selectedProduct.name;
      this.productPrice.nativeElement.value = this.data.data.selectedProduct.price;
      this.productQuantity.nativeElement.value = this.data.data.selectedProduct.quantity;
      document.getElementById('primaryBtn').innerHTML = '';
      document.getElementById('secondaryBtn').innerHTML = '';

      document
        .getElementById('primaryBtn')
        .addEventListener('click', this.editProduct.bind(this));
      document
        .getElementById('secondaryBtn')
        .addEventListener('click', this.removeProduct.bind(this));

      switch (this.data.data.selectedProduct.type) {
        case 'drink': {
          this.drinkCheck.nativeElement.checked = true;
          break;
        }
        case 'hookah': {
          this.chillCheck.nativeElement.checked = true;
          break;
        }
        case 'sandvich': {
          this.sandvichCheck.nativeElement.checked = true;
          break;
        }
        case 'hotDrinks': {
          this.hotDrinksCheck.nativeElement.checked = true;
          break;
        }
      }
    } else {
      this.secondaryTitle = '';
      document.getElementById('primaryBtn').innerHTML = '';
      document.getElementById('secondaryBtn').innerHTML = '';

      document
        .getElementById('primaryBtn')
        .addEventListener('click', this.addProduct.bind(this));
      document
        .getElementById('secondaryBtn')
        .addEventListener('click', this.cancelProcess.bind(this));
    }
  }

  addProduct() {
    let prType;

    if (this.drinkCheck.nativeElement.checked) {
      prType = 'drink';
    }
    if (this.chillCheck.nativeElement.checked) {
      prType = 'hookah';
    }
    if (this.sandvichCheck.nativeElement.checked) {
      prType = 'sandvich';
    }
    if (this.hotDrinksCheck.nativeElement.checked) {
      prType = 'hotDrinks';
    }
    const prPrice = this.productPrice.nativeElement.value.replace(',', '.');

    this.productService
      .addProduct(
        this.productName.nativeElement.value,
        parseFloat(prPrice),
        parseInt(this.productQuantity.nativeElement.value, 10),
        prType
      )
      .subscribe((products: any) => {
        products.forEach(product => {
          const obj = {
            id: product.id,
            name: product.name,
            price: product.price,
            quantity: product.quantity,
            icon: '',
            currency: '',
            class:
              product.quantity > 0
                ? 'badge badge-complete'
                : 'badge badge-danger',
            status: product.quantity > 0 ? '' : ''
          };
          if (product.type === 'drink') {
            obj.icon = '../../assets/images/shop/cola.svg';
          }
          if (product.type === 'hookah') {
            obj.icon = '../../assets/images/shop/hookah.svg';
          }
          if (product.type === 'sandvich') {
            obj.icon = '../../assets/images/shop/hamburger.svg';
          }
          if (product.type === 'hotDrinks') {
            obj.icon = '../../assets/images/shop/hotDrinks.svg';
          }
          this.data.data.shopContent.push(obj);
        });
      });

    setTimeout(() => {
      this.data.data.selectedProduct = undefined;
      this.data.changeState('productList');
    }, 100);
  }

  editProduct() {
    let prType;

    if (this.drinkCheck.nativeElement.checked) {
      prType = 'drink';
    }
    if (this.chillCheck.nativeElement.checked) {
      prType = 'hookah';
    }
    if (this.sandvichCheck.nativeElement.checked) {
      prType = 'sandvich';
    }
    if (this.hotDrinksCheck.nativeElement.checked) {
      prType = 'hotDrinks';
    }
    const prPrice = this.productPrice.nativeElement.value.replace(',', '.');
    console.error(prType);
    this.productService
      .updateSingleProduct(
        this.data.data.selectedProduct.id,
        this.productName.nativeElement.value,
        parseFloat(prPrice),
        parseInt(this.productQuantity.nativeElement.value, 10),
        prType
      )
      .subscribe(item => {
        setTimeout(() => {
          console.warn(
            this.data.data.selectedProduct.id,
            this.productName.nativeElement.value,
            parseFloat(prPrice),
            parseInt(this.productQuantity.nativeElement.value, 10),
            prType
          );
          this.data.data.selectedProduct = undefined;
          this.data.changeState('productList');
        }, 100);
      });
  }

  removeProduct() {
    this.productService
      .deleteSingleProduct(this.data.data.selectedProduct.id)
      .subscribe(Response => {
        setTimeout(() => {
          this.data.data.selectedProduct = undefined;
          this.data.changeState('productList');
        }, 100);
      });
  }

  cancelProcess() {
    this.data.data.selectedProduct = undefined;
    this.data.changeState('home');
  }
}
