import { Component, OnInit, Input } from '@angular/core';
import { AuthenticationService } from '../services/authenticate.service';

@Component({
  selector: 'app-employerlist',
  templateUrl: './employerlist.component.html',
  styleUrls: ['./employerlist.component.scss']
})
export class EmployerlistComponent implements OnInit {
  tableColumns = ['#', 'Avatar', 'ID', '', '', '', ''];
  allUsers: any;

  tableHeading = ' ';

  @Input() data;

  constructor(private authService: AuthenticationService) {
    this.authService.getAllUsers().subscribe((users: any) => {
      console.warn(users);
      this.allUsers = users;
      this.data.data.allUsers = [];
      users.forEach(user => {
        if (user.fK_Role !== 'Developer') {
          const obj = {
            id: user.id,
            name: user.firstName,
            product: user.lastName,
            quantity: user.userActive === true ? '' : ' ',
            icon: this.data.data.avatars[user.avatarId - 1],
            class:
              user.fK_Role === 'Administrator'
                ? 'badge badge-complete'
                : 'badge badge-warning',
            status:
              user.fK_Role === 'Administrator'
                ? ''
                : ''
          };
          this.data.data.allUsers.push(obj);
        }
      });
    });
  }

  ngOnInit() {}

  getUserById(id) {
    this.data.data.selectedUser = this.allUsers.filter(
      user => user.id === id
    )[0];
    setTimeout(() => {
      this.data.changeState('userProfile');
    }, 100);
  }
}
