import { Component, OnInit, ElementRef, ViewChild, Input } from '@angular/core';
import { ConsoleService } from 'src/app/services/console.service';

@Component({
  selector: 'app-register-console',
  templateUrl: './register-console.component.html',
  styleUrls: ['./register-console.component.scss']
})
export class RegisterConsoleComponent implements OnInit {
  @ViewChild('numberInput') numberInput: ElementRef;
  @ViewChild('priceInput') priceInput: ElementRef;
  @ViewChild('vipCheck') vipCheck: ElementRef;
  @ViewChild('standartCheck') standartCheck: ElementRef;

  public secondaryTitle = '';

  @Input() data;

  constructor(private consoleService: ConsoleService) {}

  ngOnInit() {
    this.init(this.data.data.selectedConsole !== undefined);
  }

  init(forEdit: boolean) {
    if (forEdit) {
      this.secondaryTitle = '';

      this.numberInput.nativeElement.value = this.data.data.selectedConsole.consoleNumber;
      this.priceInput.nativeElement.value = this.data.data.selectedConsole.standartPrice;

      this.vipCheck.nativeElement.checked =
        this.data.data.selectedConsole.type === 'VIP';
      this.standartCheck.nativeElement.checked =
        this.data.data.selectedConsole.type === 'STANDARD';

      document.getElementById('primaryBtn').innerHTML = '';
      document.getElementById('secondaryBtn').innerHTML = '';

      document
        .getElementById('primaryBtn')
        .addEventListener('click', this.updateConsole.bind(this));
      document
        .getElementById('secondaryBtn')
        .addEventListener('click', this.removeConsole.bind(this));
    } else {
      this.secondaryTitle = '';
      document.getElementById('primaryBtn').innerHTML = '';
      document.getElementById('secondaryBtn').innerHTML = '';

      document
        .getElementById('primaryBtn')
        .addEventListener('click', this.addConsole.bind(this));
      document
        .getElementById('secondaryBtn')
        .addEventListener('click', this.cancelProcess.bind(this));
    }
  }

  addConsole() {
    this.consoleService
      .addConsole(
        this.vipCheck.nativeElement.checked
          ? 'VIP CONSOLE'
          : 'STANDARD CONSOLE',
        parseInt(this.priceInput.nativeElement.value, 10),
        parseInt(this.numberInput.nativeElement.value, 10),
        this.vipCheck.nativeElement.checked ? 'VIP' : 'STANDARD'
      )
      .subscribe(cons => {
        console.log(cons);
        setTimeout(() => {
          this.data.data.selectedConsole = undefined;
          this.data.changeState('consoleList');
        }, 100);
      });
  }

  updateConsole() {
    this.consoleService
      .updateSingleConsole(
        this.data.data.selectedConsole.id,
        this.vipCheck.nativeElement.checked
          ? 'VIP CONSOLE'
          : 'STANDARD CONSOLE',
        parseInt(this.priceInput.nativeElement.value, 10),
        parseInt(this.numberInput.nativeElement.value, 10),
        this.vipCheck.nativeElement.checked ? 'VIP' : 'STANDARD'
      )
      .subscribe(cons => {
        setTimeout(() => {
          this.data.data.selectedConsole = undefined;
          this.data.changeState('consoleList');
        }, 100);
      });
  }

  removeConsole() {
    this.consoleService
      .deleteSingleConsole(this.data.data.selectedConsole.id)
      .subscribe(success => {
        setTimeout(() => {
          this.data.data.selectedConsole = undefined;
          this.data.changeState('consoleList');
        }, 100);
      });
  }

  cancelProcess() {
    this.data.data.selectedConsole = undefined;
    this.data.changeState('home');
  }
}
