import { Component, OnInit } from '@angular/core';
import { ConsoleService } from '../services/console.service';

@Component({
  selector: 'app-console-history',
  templateUrl: './console-history.component.html',
  styleUrls: ['./console-history.component.scss']
})
export class ConsoleHistoryComponent implements OnInit {
  constructor(private consoleService: ConsoleService) {}

  historyList = [];

  ngOnInit() {
    this.consoleService.getConsolesHistory().subscribe((data: any) => {
      data.forEach(singleItem => {
        const item = {
          console: singleItem.consoleName + ' ' + singleItem.consoleNumber,
          startDate: singleItem.playStart,
          endDate: singleItem.playFinish,
          author: singleItem.userFirstName + ' ' + singleItem.userLastName
        };
        this.historyList.push(item);
      });
    });
  }
}
