import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss']
})
export class TableComponent implements OnInit {
  data: any;
  columns: any;
  title: string;

  @Output() selectedId = new EventEmitter<number>();

  @Input() set tableHeading(val: any) {
    this.title = val;
  }
  @Input() set tableColumns(val: any) {
    this.columns = val;
  }
  @Input() set tableData(vals: any) {
    console.warn(vals);
    this.data = vals;
  }

  constructor() {}

  ngOnInit() {}

  displayQuantity(quantity: any) {
    return !isNaN(quantity) && quantity > -1;
  }

  displayStatus(quantity: any) {
    return isNaN(quantity);
  }

  displayStatusText(quantity: any) {
    return quantity === true ? '' : ' ';
  }

  getStatusClass(quantity: any) {
    return quantity === ''
      ? 'badge badge-complete'
      : 'badge badge-danger';
  }

  selectItem(id: number) {
    this.selectedId.emit(id);
  }
}
