import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';

// const url = 'http://playtime.ge';
const url = 'http://localhost:4000';
// const url = 'http://33cbbe80.ngrok.io';

@Injectable()
export class AuthenticationService {
  public user;
  constructor(private http: HttpClient) {}

  login(username: string, lastname: string, password: string) {
    return this.http
      .post<any>(`${url}/users/authenticate`, {
        Firstname: username,
        Lastname: lastname,
        Password: password
      })
      .pipe(
        map(user => {
          console.warn(user);
          if (user && user.token) {
            localStorage.setItem('currentUser', JSON.stringify(user));
          }
          return user;
        })
      );
  }

  register(
    firstname: string,
    lastname: string,
    password: string,
    email: string,
    number: string,
    avatarId: number,
    role: number
  ) {
    return this.http.post<any>(`${url}/users/register`, {
      Firstname: firstname,
      Lastname: lastname,
      Password: password,
      AvatarId: avatarId,
      FK_Role: role,
      Email: email,
      Number: number
    });
  }

  updateSingleUser(
    id: number,
    firstname: string,
    lastname: string,
    password: string,
    email: string,
    number: string,
    avatarId: number,
    role: number
  ) {
    return this.http.put(
      `${url}/users/${id}`,
      {
        Firstname: firstname,
        Lastname: lastname,
        Password: password,
        AvatarId: avatarId,
        FK_Role: role,
        Email: email,
        Number: number
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  logout() {
    localStorage.removeItem('currentUser');
  }

  startWorking() {
    return this.http.post(
      `${url}/users/enter`,
      {},
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  stopWorking() {
    return this.http.post(
      `${url}/users/exit`,
      {},
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  getAllUsers() {
    return this.http.get(`${url}/users`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  workInfo() {
    return this.http.get(`${url}/users/userWorkInfo`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  getUserById(id: number) {
    return this.http.get(`${url}/users/${id}`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  deleteUser(id: number) {
    return this.http.delete(`${url}/users/${id}`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  getUserStats(userID: number) {
    return this.http.get(`${url}/users/UserStatic/${userID}`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }
}
