import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ProductService } from 'src/app/services/products.service';
import { AuthenticationService } from 'src/app/services/authenticate.service';

@Component({
  selector: 'app-confirm-modal',
  templateUrl: './confirm-modal.component.html',
  styleUrls: ['./confirm-modal.component.scss']
})
export class ConfirmModalComponent implements OnInit {
  saleItems = [];
  tmpSaleItems: any[] = [];
  tmpItemsList = [];

  @Input() modalContent;
  @Input() data;
  @Output() sellItems = new EventEmitter<boolean>();
  @Output() closeModal = new EventEmitter<string>();

  constructor(
    private productService: ProductService,
    private authService: AuthenticationService
  ) {}

  ngOnInit() {
    this.tmpSaleItems = [].concat(this.modalContent.info[0]);

    // this.tmpItemsList = this.modalContent.info[1];
    // let singleItem = {
    //   name: '',
    //   quantity: 0,
    //   price: 0
    // };

    // for (let i = 0; i < this.tmpSaleItems.length; i++) {
    //   for (let j = 0; j < this.tmpItemsList.length; j++) {
    //     if (this.tmpSaleItems[i].index === j) {
    //       singleItem.name = this.tmpItemsList[j].item;
    //       singleItem.quantity = this.tmpSaleItems[i].quantity;
    //       singleItem.price =
    //         (this.tmpItemsList[j].price * 100 * this.tmpSaleItems[i].quantity) /
    //         100;
    //       this.saleItems.push(singleItem);
    //       singleItem = {
    //         name: '',
    //         quantity: 0,
    //         price: 0
    //       };
    //     }
    //   }
    // }
  }

  doAction(type) {
    switch (type) {
      case 'accept': {
        for (let i = 0; i < this.tmpSaleItems.length; i++) {
          this.productService
            .sellProduct(
              this.data.data.user.id,
              this.tmpSaleItems[i].id,
              this.tmpSaleItems[i].quantity
            )
            .subscribe(data => {});
        }
        this.sellItems.emit(true);
        this.closeModal.emit('');
        setTimeout(() => {
          this.authService.workInfo().subscribe((data: any) => {
            this.data.setValue('shop', data['magazineSum']);
            this.data.setValue('console', data['consoleSum']);
          });
        }, 500);
        break;
      }
      case 'decline': {
        this.closeModal.emit('');
        break;
      }
    }
  }
  get fullPrice() {
    let fullPrice = 0;
    this.tmpSaleItems.forEach(item => {
      fullPrice += item.price;
    });
    return fullPrice.toFixed(2);
  }
}
