import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { AuthenticationService } from '../services/authenticate.service';

@Component({
  selector: 'app-register-employe',
  templateUrl: './register-employe.component.html',
  styleUrls: ['./register-employe.component.scss']
})
export class RegisterEmployeComponent implements OnInit {
  @ViewChild('employerName') employerName: ElementRef;
  @ViewChild('employerSurName') employerSurName: ElementRef;
  @ViewChild('employerPassword') employerPassword: ElementRef;
  @ViewChild('employerMail') employerMail: ElementRef;
  @ViewChild('employerMobile') employerMobile: ElementRef;

  @ViewChild('adminCheck') adminCheck: ElementRef;
  @ViewChild('userCheck') userCheck: ElementRef;

  public employerAvatar = 0;
  public secondaryTitle = '';

  @Input() data;

  constructor(private authService: AuthenticationService) {}

  ngOnInit() {
    console.warn(this.data.data.user);
    this.init(this.data.data.selectedUser !== undefined);
  }

  selectAvatar(id: any) {
    this.employerAvatar = id;
  }

  registerUser() {
    this.authService
      .register(
        this.employerName.nativeElement.value,
        this.employerSurName.nativeElement.value,
        this.employerPassword.nativeElement.value,
        this.employerMail.nativeElement.value,
        this.employerMobile.nativeElement.value,
        this.employerAvatar + 1,
        this.adminCheck.nativeElement.checked ? 2 : 3
      )
      .subscribe(user => {
        setTimeout(() => {
          this.data.data.selectedUser = undefined;
          this.data.changeState('employeeList');
        }, 100);
      });
  }

  updateUser() {
    this.authService
      .updateSingleUser(
        this.data.data.selectedUser.id,
        this.employerName.nativeElement.value,
        this.employerSurName.nativeElement.value,
        this.employerPassword.nativeElement.value,
        this.employerMail.nativeElement.value,
        this.employerMobile.nativeElement.value,
        this.employerAvatar + 1,
        this.adminCheck.nativeElement.checked ? 2 : 3
      )
      .subscribe(user => {
        setTimeout(() => {
          this.data.data.selectedUser = undefined;
          this.data.changeState('employeeList');
        }, 100);
      });
  }

  removeUser() {
    this.authService
      .deleteUser(this.data.data.selectedUser.id)
      .subscribe(succes => {
        setTimeout(() => {
          this.data.data.selectedUser = undefined;
          this.data.changeState('employeeList');
        }, 100);
      });
  }
  init(forEdit: boolean) {
    if (forEdit) {
      this.secondaryTitle = '';
      this.employerName.nativeElement.value = this.data.data.selectedUser.firstName;
      this.employerSurName.nativeElement.value = this.data.data.selectedUser.lastName;
      this.employerAvatar = this.data.data.selectedUser.avatarId - 1;
      this.employerMail.nativeElement.value = this.data.data.selectedUser.email;
      this.employerMobile.nativeElement.value = this.data.data.selectedUser.number;
      this.adminCheck.nativeElement.value = this.data.data.selectedUser.fK_Role;
      document.getElementById('primaryBtn').innerHTML = '';
      document.getElementById('secondaryBtn').innerHTML = '';

      document
        .getElementById('primaryBtn')
        .addEventListener('click', this.updateUser.bind(this));
      document
        .getElementById('secondaryBtn')
        .addEventListener('click', this.removeUser.bind(this));

      switch (this.data.data.selectedUser.fK_Role) {
        case 'Administrator': {
          this.adminCheck.nativeElement.checked = true;
          break;
        }
        case 'Customer': {
          this.userCheck.nativeElement.checked = true;
          break;
        }
      }
    } else {
      this.secondaryTitle = '';
      document.getElementById('primaryBtn').innerHTML = '';
      document.getElementById('secondaryBtn').innerHTML = '';

      document
        .getElementById('primaryBtn')
        .addEventListener('click', this.registerUser.bind(this));
      document
        .getElementById('secondaryBtn')
        .addEventListener('click', this.cancelProcess.bind(this));
    }
  }

  cancelProcess() {
    this.data.data.selectedUser = undefined;
    this.data.changeState('home');
  }
}
