import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-single-computer',
  templateUrl: './single-computer.component.html',
  styleUrls: ['./single-computer.component.scss']
})
export class SingleComputerComponent implements OnInit {
  soundIcon = '../../assets/images/sound/speaker.svg';
  muteIcon = '../../assets/images/sound/mute.svg';
  haveSound = true;

  tickAudio = new Audio('../../assets/low-time.mp3');

  currentIcon = '';
  computerName = '';
  computerID = 0;
  computerNumber = 0;
  computerState = 'free';
  computerType = 'standart';
  computerPrice = 0;
  promotionPrice = null;
  currentCost: any = 0;
  crtTime: any = 0;

  timeSettings = {
    controllers: 0,
    time: 0
  };

  @Input() data;
  @Input() computer;
  @Input() currentTime;
  @Input() maxTime;
  @Input() fixedTime;
  @Input() timerActive;

  @Input() set name(value: string) {
    this.computerName = value;
  }

  @Input() set price(value: number) {
    this.computerPrice = value;
  }

  @Input() set promPrice(value: string) {
    this.promotionPrice = value;
  }

  @Input() set id(value: number) {
    this.computerID = value;
  }

  @Input() set number(value: number) {
    this.computerNumber = value;
  }

  @Input() set state(value: string) {
    this.computerState = value;
    this.currentIcon = this.soundIcon;
  }
  @Input() set type(value: string) {
    this.computerType = value;
  }

  @Output() start = new EventEmitter<any>();
  @Output() stop = new EventEmitter<any>();

  constructor() {}

  ngOnInit() {
    this.setPrice();
  }

  get currentPrice() {
    if (this.promotionPrice !== null) {
      return this.promotionPrice;
    } else {
      return this.computerPrice;
    }
  }

  setSettings(msg: string, ev: any) {
    switch (msg) {
      case 'controller': {
        this.timeSettings.controllers = ev.value;
        return;
      }
      case 'time': {
        this.timeSettings.time = ev.value;
        return;
      }
    }
  }

  setPrice() {}

  newTime(ev) {
    if (this.haveSound && ev < 120 && this.fixedTime === true) {
      this.tickAudio.play();
    }
    switch (this.maxTime) {
      case 0: {
        this.crtTime = ev;
        this.currentCost = parseFloat(
          ((ev / 3600) * this.computerPrice).toString()
        ).toFixed(2);
        break;
      }
      default: {
        const newTime = this.maxTime - ev;
        this.currentCost = parseFloat(
          ((newTime / 3600) * this.computerPrice).toString()
        ).toFixed(2);
        break;
      }
    }
  }

  changeSound() {
    if (this.haveSound) {
      this.currentIcon = this.muteIcon;
    } else {
      this.currentIcon = this.soundIcon;
    }
    this.haveSound = !this.haveSound;
  }

  startTime() {
    this.start.emit(this.timeSettings);
  }
  stopTimer() {
    this.stop.emit(this.timeSettings);
  }

  get controllers() {
    return this.data.data.controllers;
  }
  get time() {
    return this.data.data.time;
  }
}
