import { Component, OnInit, Input } from '@angular/core';
import { ConsoleService } from 'src/app/services/console.service';

@Component({
  selector: 'app-consolelist',
  templateUrl: './consolelist.component.html',
  styleUrls: ['./consolelist.component.scss']
})
export class ConsolelistComponent implements OnInit {
  tableColumns = ['#', 'Icon', 'ID', ' ', '', ''];
  tableContent = [];

  tableHeading = '';
  @Input() data;

  constructor(private consoleService: ConsoleService) {}

  ngOnInit() {
    this.consoleService.getallConsoles().subscribe((data: any) => {
      data.forEach(singleTable => {
        const shortedTable = {
          id: singleTable.id,
          name: singleTable.name,
          product: singleTable.currentPrice + ' ',
          danger: false,
          icon:
            singleTable.type === 'VIP'
              ? '../../../assets/images/consoles/xbox.svg'
              : '../../../assets/images/consoles/ps4.svg',
          class:
            singleTable.type === 'VIP'
              ? 'badge badge-warning'
              : 'badge badge-complete',
          status: singleTable.type === 'VIP' ? 'VIP' : ''
        };

        this.tableContent.push(shortedTable);
      });
    });
  }

  selectConsole(id) {
    this.consoleService.getConsoleById(id).subscribe(singleConsole => {
      this.data.data.selectedConsole = singleConsole;
      this.data.changeState('addconsole');
    });
  }
}
