import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

// const url = 'http://playtime.ge';
const url = 'http://localhost:4000';
// const url = 'http://33cbbe80.ngrok.io';

@Injectable()
export class ConsoleService {
  constructor(private http: HttpClient) {}

  getallConsoles() {
    return this.http.get(`${url}/consoles`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  getConsoleById(id: number) {
    return this.http.get(`${url}/consoles/${id}`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  getConsolesHistory() {
    return this.http.get(`${url}/consoles/ConsoleHistories`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  consolesFullCost() {
    return this.http.get(`${url}/consoles/ConsolePlayToDay`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  deleteSingleConsole(id: number) {
    return this.http.delete(`${url}/consoles/${id}`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  updateSingleConsole(
    id: number,
    ConsoleName: string,
    ConsolePrice: number,
    number: number,
    type: string
  ) {
    return this.http.put(
      `${url}/consoles/${id}`,
      {
        ConsoleName: ConsoleName,
        StandartPrice: ConsolePrice,
        ConsoleNumber: number,
        Type: type
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  addConsole(
    ConsoleName: string,
    ConsolePrice: number,
    number: number,
    type: string
  ) {
    return this.http.post<any>(
      `${url}/consoles/create`,
      {
        ConsoleName: ConsoleName,
        StandartPrice: ConsolePrice,
        ConsoleNumber: number,
        Type: type
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  startTime(
    userID: any,
    consoleID: any,
    controllersNum: any,
    defaultPrice: any,
    isFixed: boolean,
    time: any
  ) {
    return this.http.post<any>(
      `${url}/consoles/ConsolePlay`,
      {
        UserId: userID,
        ConsoleId: consoleID,
        Gamer: controllersNum,
        Price: defaultPrice,
        isFixedTime: isFixed,
        FixedTime: time
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  stopTime(operationID: any) {
    return this.http.post<any>(
      `${url}/consoles/ConsoleFinish/${operationID}`,
      {
        consolePlayId: operationID
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  pauseTime(operationID: any) {
    return this.http.post<any>(
      `${url}/consoles/ConsolePause/${operationID}`,
      {
        consolePlayId: operationID
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  resumeTime(operationID: any) {
    return this.http.post<any>(
      `${url}/consoles/ConsoleContinue/${operationID}`,
      {
        consolePlayId: operationID
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }
}
