import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

// const url = 'http://playtime.ge';
const url = 'http://localhost:4000';
// const url = 'http://33cbbe80.ngrok.io';

@Injectable()
export class PromotionsService {
  public user;

  constructor(private http: HttpClient) {}

  addPromotion(
    promotionName: string,
    promotionDescription: string,
    promotionPrice: number,
    promotionIsAllTime: boolean,
    promotionStartDate: string,
    prommotionEndDate: string,
    consoleList: number[],
    startHour: string,
    endHour: string
  ) {
    return this.http.post<any>(
      `${url}/promotions/create`,
      {
        PromotionName: promotionName,
        PromotionDesc: promotionDescription,
        Price: promotionPrice,
        AllTime: promotionIsAllTime,
        DateStart: promotionStartDate,
        DateEnd: prommotionEndDate,
        Consoles: consoleList,
        HourStart: startHour,
        HourEnd: endHour
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  getAllPromotions() {
    return this.http.get(`${url}/promotions`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  getPromotionById(id: number) {
    return this.http.get(`${url}/promotions/${id}`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }

  updateSingleProMotionn(
    id: number,
    promotionName: string,
    promotionDescription: string,
    promotionPrice: number,
    promotionIsAllTime: boolean,
    promotionStartDate: string,
    prommotionEndDate: string,
    consoleList: number[]
  ) {
    return this.http.put(
      `${url}/promotions/${id}`,
      {
        PromotionName: promotionName,
        PromotionDesc: promotionDescription,
        Price: promotionPrice,
        AllTime: promotionIsAllTime,
        DateStart: promotionStartDate,
        DateEnd: prommotionEndDate,
        Consoles: consoleList
      },
      {
        headers: new HttpHeaders({
          Authorization:
            'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
        })
      }
    );
  }

  deleteSinglePromotion(id: number) {
    return this.http.delete(`${url}/promotions/${id}`, {
      headers: new HttpHeaders({
        Authorization:
          'Bearer ' + JSON.parse(localStorage.getItem('currentUser')).token
      })
    });
  }
}
