import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConsoleService } from 'src/app/services/console.service';
import { AuthenticationService } from 'src/app/services/authenticate.service';

@Component({
  selector: 'app-stop-time',
  templateUrl: './stop-time.component.html',
  styleUrls: ['./stop-time.component.scss']
})
export class StopTimeComponent implements OnInit {
  fullPrice = 0;
  @Input() data;
  @Input() modalContent;
  @Output() stopTimer = new EventEmitter<number>();
  @Output() closeModal = new EventEmitter<string>();

  constructor(
    private consoleService: ConsoleService,
    private authService: AuthenticationService
  ) {}

  ngOnInit() {}

  doAction(type) {
    switch (type) {
      case 'accept': {
        this.consoleService
          .stopTime(this.modalContent.actionID)
          .subscribe(data => {
            this.closeModal.emit('');
            this.stopTimer.emit(this.modalContent.info.consoleId);
            this.data.data.computers.forEach(singleConsole => {
              if (singleConsole.id === this.modalContent.info.consoleId) {
                singleConsole.state = 'free';
              }
            });
          });

        setTimeout(() => {
          this.authService.workInfo().subscribe((data: any) => {
            this.data.setValue('shop', data['magazineSum']);
            this.data.setValue('console', data['consoleSum']);
          });
        }, 1000);
        break;
      }
      case 'decline': {
        this.consoleService
          .resumeTime(this.modalContent.actionID)
          .subscribe(data => {
            this.closeModal.emit('');
            this.data.data.computers.forEach(singleConsole => {
              if (singleConsole.id === this.modalContent.info.consoleId) {
                singleConsole.timerActive = true;
              }
            });
          });
        break;
      }
    }
  }
}
