import { Component, OnInit, Input } from '@angular/core';
import { ConsoleService } from '../services/console.service';
import { AuthenticationService } from '../services/authenticate.service';

@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss']
})
export class ContentComponent implements OnInit {
  data: any;
  userActive = false;

  @Input()
  set appData(val: any) {
    this.data = val;
  }

  constructor(
    private consoleService: ConsoleService,
    private authService: AuthenticationService
  ) {}

  ngOnInit() {
    this.authService
      .getUserById(this.data.data.user.id)
      .subscribe((user: any) => {
        this.userActive = user.fK_Role === 3 ? user.userActive : true;
      });
  }

  get state() {
    return this.data.gameState;
  }

  get tableContent() {
    return this.data.data.tableContent;
  }
  get tableHeading() {
    return this.data.data.tableHeading;
  }
  get computers() {
    return this.data.data.computers;
  }

  get WidgetContent() {
    return this.data.data.WidgetContent;
  }

  startTime(computer, ev) {
    setTimeout(() => {
      if (this.userActive) {
        const isFixed = ev.time !== 0;
        let time = 0;
        switch (ev.time) {
          case 8: {
            time = 3600 / 4;
            break;
          }
          case 7: {
            time = (3600 / 4) * 3;
            break;
          }
          case 5: {
            time = 3600 / 2;
            break;
          }
          case 6: {
            time = 3600 + 3600 / 2;
            break;
          }
          default: {
            time = ev.time * 3600;
            break;
          }
        }
        this.consoleService
          .startTime(
            this.data.data.user.id,
            computer.id,
            ev.controllers,
            0,
            isFixed,
            time
          )
          .subscribe(data => {
            this.computers.forEach(cons => {
              if (cons.id === data.consoleId) {
                cons.currentPrice = data.price;
                cons.maxTime = parseInt(data.fixedTime, 10);
                cons.currentTime = parseInt(data.fixedTime, 10);
                cons.fixedTime = data.isFixedTime;
                cons.timerActive = true;
                cons.users = data.gamer;
                cons.actionID = data.id;
                cons.state = 'busy';
              }
            });
          });
      } else {
        this.data.data.currentModal = 'informationModal';
        this.data.data.modalParams[this.data.data.currentModal].infoText =
          '   ..!';
        this.data.data.modalParams[this.data.data.currentModal].name =
          'Warning';
      }
    }, 500);
  }

  stopTimer(computer, ev) {
    this.consoleService.pauseTime(computer.actionID).subscribe(data => {
      // console.warn(data);
      this.data.data.currentModal = 'stopTimerModal';
      this.data.data.modalParams.stopTimerModal.info = data;
      this.data.data.modalParams.stopTimerModal.consoleName = computer.name;
      this.data.data.modalParams.stopTimerModal.actionID = computer.actionID;
      this.data.data.computers.forEach(singleConsole => {
        if (singleConsole.id === computer.id) {
          singleConsole.timerActive = false;
        }
      });
    });
  }
}
