class AppData {
  data: any = {
    currentModal: '',
    userStatus: 'unAuthorized',
    user: {
      id: '',
      firstName: '',
      lastName: '',
      avatarId: '',
      email: '',
      number: 0,
      userRole: '',
      createDate: '',
      token: ''
    },
    gameState: 'home',
    WidgetContent: [
      {
        id: 'shop',
        iconClass: 'pe-7s-cart',
        color: '#0000ff',
        currency: '',
        title: ' ',
        value: 0
      },
      {
        id: 'console',
        iconClass: 'pe-7s-monitor',
        color: '#00ff00',
        currency: '',
        title: ' ',
        value: 0
      }
    ],
    tableHeading: ' ',
    computers: [],
    controllers: [
      { value: 2, name: '2 ' },
      { value: 3, name: '3 ' },
      { value: 4, name: '4 ' }
    ],
    time: [
      { value: 0, name: '' },
      { value: 8, name: '15 ' },
      { value: 5, name: '30 ' },
      { value: 7, name: '45 ' },
      { value: 1, name: '1 ' },
      { value: 6, name: '1,5 ' },
      { value: 2, name: '2 ' },
      { value: 3, name: '3 ' },
      { value: 4, name: '4 ' }
    ],
    shopContent: [],
    itemsToSale: [],
    selectedProduct: undefined,
    selectedPromotion: undefined,
    selectedConsole: undefined,
    selectedUser: undefined,
    allUsers: [],
    modalParams: {
      confirmSaleModal: {
        type: 'shopModal',
        name: 'Confirm Sale',
        buttons: [
          {
            text: '',
            type: 'decline'
          },
          {
            text: '',
            type: 'accept'
          }
        ],
        info: [],
        actionID: 0,
        consoleName: '',
        color: '#000000'
      },
      userInfoModal: {
        type: 'salesModal',
        name: 'User Sales',
        buttons: [
          {
            text: 'OK',
            type: 'accept'
          }
        ],
        info: [],
        actionID: 0,
        consoleName: '',
        color: '#000000'
      },
      informationModal: {
        type: 'infoModal',
        name: 'Success',
        infoText: '  !',
        color: '#000000'
      },
      stopTimerModal: {
        type: 'timeModal',
        name: 'Stop Timer',
        buttons: [
          {
            text: '',
            type: 'decline'
          },
          {
            text: '',
            type: 'accept'
          }
        ],
        info: [],
        color: '#000000'
      }
    },
    avatars: [
      '../assets/avatar/avatar-1.svg',
      '../assets/avatar/avatar-2.svg',
      '../assets/avatar/avatar-3.svg',
      '../assets/avatar/avatar-4.svg',
      '../assets/avatar/avatar-5.svg',
      '../assets/avatar/avatar-6.svg',
      '../assets/avatar/avatar-7.svg',
      '../assets/avatar/avatar-8.svg',
      '../assets/avatar/avatar-9.svg',
      '../assets/avatar/avatar-10.svg'
    ],

    promotions: [
      {
        id: 1,
        title: ' 2+1',
        shortInfo: '2. = 3.',
        consoles: [' 1', ' 2', ' 3'],
        allTime: false,
        allTimeText: '',
        dateFrom: '15 .',
        dateTo: '15 .',
        price: 4,
        currency: '',
        fullInfo:
          '    2       .'
      },
      {
        id: 2,
        title: ' ',
        shortInfo: '1. = 2',
        consoles: [' 1', ' 2'],
        allTime: true,
        allTimeText: '',
        dateFrom: '',
        dateTo: '',
        price: 2,
        currency: '',
        fullInfo:
          '  1      VIP   1    2.'
      }
    ]
  };

  changeState(val: string): void {
    this.data.gameState = val;
  }

  setValue(id: string, value: number): void {
    this.data.WidgetContent.forEach(widget => {
      if (widget.id === id) {
        widget.value = value.toFixed(2);
      }
    });
  }

  removePromotion(id: number) {
    this.data.promotions = this.data.promotions.filter(
      promo => id !== promo.id
    );
  }

  addProduct(vall: any) {
    this.data.shopContent.push(vall);
  }
}

export default new AppData();
