import { Component, OnInit, Input } from '@angular/core';
import { PromotionsService } from 'src/app/services/promotions.service';

@Component({
  selector: 'app-promolist',
  templateUrl: './promolist.component.html',
  styleUrls: ['./promolist.component.scss']
})
export class PromolistComponent implements OnInit {
  promotionList: any[];

  @Input() set promotions(val: any[]) {
    this.promotionList = val;
  }
  @Input() data;

  constructor(private promotionService: PromotionsService) {}

  ngOnInit() {
    this.getAllPromotions();
  }

  getDate(str: string) {
    const months = [
      '.',
      '.',
      '.',
      '.',
      '.',
      '.',
      '.',
      '.',
      '.',
      '.',
      '.',
      '.'
    ];
    if (str === null) {
      return null;
    }
    const day = str.split('/')[0];
    const month = parseInt(str.split('/')[1], 10) - 1;
    return `${day} ${months[month]}`;
  }

  getAllConsoles(promId: any, allConnections: any, allConsoles: any) {
    const tmpConnections = allConnections.filter(
      promt => promt.promotionId === promId
    );

    const tmpConsoles = [];

    tmpConnections.forEach(tmpID => {
      allConsoles.forEach(cons => {
        if (tmpID.consoleId === cons.id) {
          tmpConsoles.push(`${cons.consoleName} #${cons.consoleNumber}`);
        }
      });
    });

    return tmpConsoles;
  }

  getAllPromotions() {
    const fullPromList = [];
    let allPromotions: any;
    let allConnections: any;
    let allConsoles: any;

    this.promotionService.getAllPromotions().subscribe(promotions => {
      allPromotions = promotions['promotionDtos'];
      allConnections = promotions['consPromDtos'];
      allConsoles = promotions['consoleDtos'];

      allPromotions.forEach(prom => {
        console.warn(prom);
        const tmpProm = {
          id: prom.id,
          title: prom.promotionName,
          shortInfo: `${prom.price} `,
          consoles: this.getAllConsoles(prom.id, allConnections, allConsoles),
          allTime: prom.allTime,
          allTimeText: prom.allTime ? '' : '',
          dateFrom: this.getDate(prom.dateStart),
          dateFromCalendar: prom.dateStart,
          dateTo: this.getDate(prom.dateEnd),
          dateToCalendar: prom.dateEnd,
          hourStart: prom.hourStart,
          hourEnd: prom.hourEnd,
          price: prom.price,
          currency: '',
          fullInfo: prom.promotionDesc
        };
        fullPromList.push(tmpProm);
      });
    });
    this.data.data.promotions = fullPromList;
  }

  removePromo(ev) {
    this.promotionService.deleteSinglePromotion(ev).subscribe(() => {
      this.getAllPromotions();
    });
  }

  editPromotion(id) {
    this.data.data.selectedPromotion = this.data.data.promotions.filter(
      promo => promo.id === id
    );
    this.data.data.gameState = 'addpromo';
  }
}
