import { Component, OnInit, ViewChild, ElementRef, Input } from '@angular/core';
import { PromotionsService } from 'src/app/services/promotions.service';

@Component({
  selector: 'app-register-promo',
  templateUrl: './register-promo.component.html',
  styleUrls: ['./register-promo.component.scss']
})
export class RegisterPromoComponent implements OnInit {
  @ViewChild('promotionName') promotionName: ElementRef;
  @ViewChild('promotionPrice') promotionPrice: ElementRef;
  @ViewChild('promotionDescription') promotionDescription: ElementRef;

  @ViewChild('startHour') promotionStartHour: ElementRef;
  @ViewChild('endHour') promotionEndHour: ElementRef;

  @ViewChild('allTimeCheck') allTimeCheck: ElementRef;

  consoles = [];
  allTime = false;
  startDate = undefined;
  endDate = undefined;
  secondaryTitle = '';

  @Input() data;

  constructor(private promotionService: PromotionsService) {}

  ngOnInit() {}

  getDate(ev) {
    const newDate = `${ev.day}/${ev.month}/${ev.year}`;
    if (this.startDate === undefined) {
      this.startDate = newDate;
    } else if (this.startDate !== newDate && this.endDate !== undefined) {
      this.startDate = newDate;
      this.endDate = undefined;
    } else {
      this.endDate = newDate;
    }
  }

  checkConsole(ev, id) {
    switch (ev.target.checked) {
      case true: {
        this.consoles.push(id);
        break;
      }
      case false: {
        this.consoles.splice(this.consoles.indexOf(id), 1);
        break;
      }
    }
  }

  isAllTime(ev) {
    this.allTime = ev.target.checked;
  }

  createPromotion() {
    this.promotionService
      .addPromotion(
        this.promotionName.nativeElement.value,
        this.promotionDescription.nativeElement.value,
        this.promotionPrice.nativeElement.value,
        this.allTime,
        this.startDate,
        this.endDate,
        this.consoles,
        // this.convertToHour(this.promotionStartHour.nativeElement.value),
        // this.convertToHour(this.promotionEndHour.nativeElement.value)
        '16:15:00',
        '16:17:00'
      )
      .subscribe(promo => {
        console.warn(promo);
        setTimeout(() => {
          this.data.changeState('promoList');
        }, 100);
      });
  }
  cancelProcess() {
    this.data.changeState('home');
  }
  convertToHour(str: string) {
    const suFix = parseInt(str, 10) < 12 ? 'AM' : 'PM';
    const netTime =
      parseInt(str, 10) < 12 ? parseInt(str, 10) : parseInt(str, 10) - 12;
    return `${netTime}:00:00 ${suFix}`;
  }
}
